html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.header {
  background-color: #565656;
}
.header__barra {
  display: flex;
  flex-direction: column;
  margin: 0 8rem;
}
@media (min-width: 768px) {
  .header__barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__nav-principal {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .header__nav-principal {
    flex-direction: row;
    gap: 3rem;
    margin-top: 0;
  }
}
.header__nav-principal a {
  text-align: center;
  color: #ffffff;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header__nav-principal a {
    margin-bottom: 0;
  }
}
.header__nav-principal a.activo {
  color: #ffd000;
}
.header__nav-principal a:hover {
  color: #ffd000;
}

.presentacion {
  background-image: url("../../img/fondo1.png");
}

.presentacion {
  margin-top: 5rem;
}
.presentacion__grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
  grid-row: 1;
  /*
  grid-template-rows: repeat(2, 1fr);
  */
}
@media (min-width: 768px) {
  .presentacion__grid {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.presentacion__contenido {
  margin: auto;
  text-align: center;
}
.presentacion__heading {
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .presentacion__heading {
    font-size: 4rem;
  }
}
.presentacion__heading span {
  color: #fdd835;
  text-transform: uppercase;
}
.presentacion__texto {
  font-weight: 700;
}
.presentacion__imagen {
  margin: 0 auto;
  width: 40%;
}
@media (min-width: 768px) {
  .presentacion__imagen {
    display: grid;
    justify-content: center;
    order: unset;
    width: 100%;
  }
}
.presentacion__imagen img {
  border-radius: 100%;
}